"use client";

import { useStocks } from "@/hooks/useStocks";
import Link from "next/link";
import { useState } from "react";
import { Search } from "lucide-react";

export default function StocksIndex() {
    const { data: stocks, isLoading } = useStocks({});
    const [search, setSearch] = useState("");

    const filteredStocks = stocks?.filter(s =>
        s.symbol.toLowerCase().includes(search.toLowerCase()) ||
        s.name?.toLowerCase().includes(search.toLowerCase())
    ) || [];

    return (
        <div className="max-w-4xl mx-auto py-8">
            <div className="mb-8 text-center">
                <h1 className="text-3xl font-bold text-gray-900 dark:text-white mb-2">Stock Insights</h1>
                <p className="text-gray-500 dark:text-gray-400">Select a stock to view detailed analysis</p>
            </div>

            <div className="relative max-w-md mx-auto mb-8">
                <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400 w-5 h-5" />
                <input
                    type="text"
                    placeholder="Search symbol (e.g. RELIANCE)..."
                    className="w-full pl-10 pr-4 py-3 rounded-xl border border-gray-200 dark:border-gray-700 bg-white dark:bg-gray-800 text-gray-900 dark:text-white focus:ring-2 focus:ring-blue-500 outline-none shadow-sm"
                    value={search}
                    onChange={(e) => setSearch(e.target.value)}
                />
            </div>

            {isLoading ? (
                <div className="text-center py-12">
                    <div className="animate-spin rounded-full h-8 w-8 border-t-2 border-b-2 border-blue-500 mx-auto"></div>
                </div>
            ) : (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                    {filteredStocks.map((stock) => (
                        <Link
                            key={stock.symbol}
                            href={`/stocks/${stock.symbol}`}
                            className="block p-4 bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 hover:border-blue-500 dark:hover:border-blue-500 transition-colors shadow-sm hover:shadow-md"
                        >
                            <div className="flex justify-between items-start">
                                <div>
                                    <div className="font-bold text-gray-900 dark:text-white">{stock.symbol}</div>
                                    <div className="text-sm text-gray-500 dark:text-gray-400 truncate max-w-[150px]">{stock.name}</div>
                                </div>
                                <div className={`text-sm font-medium ${stock.current_change_pct >= 0 ? 'text-green-600' : 'text-red-600'}`}>
                                    {stock.current_change_pct > 0 ? "+" : ""}{stock.current_change_pct.toFixed(2)}%
                                </div>
                            </div>
                            <div className="mt-3 flex justify-between items-end">
                                <div className="text-lg font-mono text-gray-900 dark:text-white">â‚¹{stock.current_price.toFixed(2)}</div>
                                <div className="text-xs text-gray-400">{stock.sector}</div>
                            </div>
                        </Link>
                    ))}

                    {filteredStocks.length === 0 && (
                        <div className="col-span-full text-center py-12 text-gray-500">
                            No stocks found matching "{search}"
                        </div>
                    )}
                </div>
            )}
        </div>
    );
}
